footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    border-top: 5px solid var(--color-bg-variant);
}
footer a {
    color: var(--color-bg);
}
.footer__logo {
    display: inline-block;
    width:100px;
    margin-bottom: 2rem;
}
.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}
.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display:flex;
    border: 1px solid transparent;
}
.footer__socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color_bg);
}
.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}
/* Media Queries for Small devices - Mobiles */

@media screen and (max-width:600px) {
    .permalinks {
        flex-direction: column;
        gap:1.5rem;
    }
    .footer__socials {
        margin-bottom: 1.5rem;
    }
}